<template>
	<tabbar
		v-if="$route.meta.tabbar"
		:active="active"
		@change="onChange"
		inactive-color='#A1BDE1'
		router
	>
		<tabbar-item name="index" to="/index">
			<span :class="{active:active == 'index'}">{{$t('home')}}</span>
			<img v-if="active != 'index'" src="../assets/index.png" slot='icon' />
			<img v-else src="../assets/indexActive.png" slot='icon' />
		</tabbar-item>
		<tabbar-item name="income" to="/income">
			<span :class="{active:active == 'income'}">{{$t('income')}}</span>
			<img v-if="active != 'income'" src="../assets/income.png" slot='icon' />
			<img v-else src="../assets/incomeActive.png" slot='icon' />
		</tabbar-item>
		<tabbar-item name="exchange" to="/exchange">
			<span :class="{active:active == 'exchange'}">{{$t('swap')}}</span>
			<img v-if="active != 'exchange'" src="../assets/exchange.png" slot='icon' />
			<img v-else src="../assets/exchangeActive.png" slot='icon' />
		</tabbar-item>
		<tabbar-item name="setting" to="/setting">
			<span :class="{active:active == 'setting'}">{{$t('setting')}}</span>
			<img v-if="active != 'setting'" src="../assets/setting.png" slot='icon' />
			<img v-else src="../assets/settingActive.png" slot='icon' />
		</tabbar-item>
	</tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant'

export default {
  name: 'self-tabbar',
  components: { Tabbar, TabbarItem },
  data() {
    return {
      active: 'index',
    }
  },
  mounted() {
    this.active = this.$route.name
  },
  methods:{
	onChange(event) {
	    this.active = event
	}
  },
  watch:{
	  $route(){
		  this.active = this.$route.name
	  }
  }
}
</script>

<style lang="scss" scoped>
.van-tabbar{
	background-color: #273446;
	border: none;
}
.van-tabbar:after{
	border: none;
	border-top: 1px solid #000;
}
.van-iconfont{
  font-size: 22px !important;
}
.van-tabbar-item__icon img{
	height: 22px;
}
.active{
	color: #21C17A!important;
}
// .iconHome{
// 	font-size: 48px !important;
// }

</style>