import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import 'amfe-flexible/index.min.js'
import router from './router'
import { Lazyload } from 'vant'
import VueClipboard from 'vue-clipboard2'
import selfIcon from '@/components/self-icon.vue'
import { $web3, contract, fromWei, toWei, toTime, upFixed, numFixed, BN, guide , currentTime } from './static/initData'
import i18n from '@/i18n/i18n.js'
Vue.config.productionTip = false
Vue.component('self-icon', selfIcon)
Vue.use(Lazyload)
Vue.use(VueClipboard)

class ShowWallet {
	constructor() {
		this.show = true
	}
	
	set(arg) {
		this.show = arg
	}
}

Vue.prototype.$web3 = $web3
Vue.prototype.$contract = contract
Vue.prototype.$fromWei = fromWei
Vue.prototype.$toWei = toWei
Vue.prototype.$toTime = toTime
Vue.prototype.$upFixed = upFixed
Vue.prototype.$numFixed = numFixed
Vue.prototype.$BN = BN
Vue.prototype.$currentTime = currentTime
Vue.prototype.$cache = {}
Vue.prototype.$guide = guide
Vue.prototype.$showWallet = new ShowWallet()

initAddress(() => {
	new Vue({
		router,
		i18n,
		render: h => h(App)
	}).$mount('#app')
})

async function initAddress(callback) {
	
	if (!guide) {
		const ChainId = await $web3.eth.net.getId()
		contract.setData(ChainId)
	}
	
	try{
		if (window.imToken) {
			imToken.callAPI("user.getCurrentAccount", (err, address) => {
				if (!err) {
					Vue.prototype.$address = address
					callback()
				}
			})
		} else if (window.ethereum.isTrust || window.ethereum.isHuobi || window.ethereum.isXWallet) {
			Vue.prototype.$address = ethereum.address
			callback()
		} else if (window.ethereum) {
			ethereum.enable().then(res => {
				Vue.prototype.$address = ethereum.selectedAddress
				callback()
				ethereum.on("accountsChanged", accounts => {
					let address = Vue.prototype.$address
					if (accounts[0] != address) {
						Vue.prototype.$address = accounts[0]
						router.go(0)
					}
				})
			})
		} else {
			Vue.prototype.$address = ''
			callback()
		}		
	}catch(e){
		Vue.prototype.$address = ''
		callback()
	}
}
