<template>
	<div class="van-tabbar-page">
		<!-- <div class="indexTopBg"></div> -->
		<div class="getHelp">
			<div class="getHelpDetails" @click="$router.push('/incomeDetail')">
				<div class="topLeft">
					<img class="lv" v-if="lv == '1'" src="../../../assets/lv1.png" />
					<img class="lv" v-if="lv == '2'" src="../../../assets/lv2.png" />
					<img class="lv" v-if="lv == '3'" src="../../../assets/lv3.png" />
					<img class="lv" v-if="lv == '4'" src="../../../assets/lv4.png" />
					<img class="lv" v-if="lv == '5'" src="../../../assets/lv5.png" />
					<img class="vip" src="../../../assets/vip.png" />
				</div>
				<div class="topRight">
					<span>{{$t('IncomeDetails')}}</span>
					<img src="../../../assets/doubleRight.png" />
				</div>
				<div class="reveiceIncome">
					<span>{{rewardAmount}}</span>
					<div>{{$t('CurrentIncome')}}</div>
				</div>
				<div class="orderInfo">
					<div>
						<span>{{$t('Available01')}}</span>
						<span>{{Amount}}</span>
					</div>
					<span></span>
					<div>
						<span>{{$t('TotalRevenue')}}</span>
						<span>{{totalRewardedAmount}}</span>
					</div>
				</div>
				<div class="incomeBg"></div>
			</div>
			
			<div class="income">
				<div class="title">
					<span>{{$t('extract')}}</span>
				</div>
				<div class="input">
					<div>
						<input type="number" :placeholder="$t('PleaseEnterMultipleOf10')" v-model="value" @input="input">
					</div>
					<span>USDT</span>
				</div>
					<!-- <div class="input">
						<input type="number" placeholder="消耗燃料" v-model="ConsumeDT" disabled>
						<span>DT</span>
					</div> -->
				<div class="Underline ConsumeDT">
					<span>{{$t('FuelConsumption')}}</span>
					<span>{{ConsumeDT}} DT</span>
				</div>
				<div class="AvailableDT">
					<span>{{$t('Available')}}：</span>
					<span>{{DT}} DT</span>
				</div>
				<!-- <span>可用 {{DT}} DT</span> -->
				<Button color="linear-gradient(44deg, #21C17A, #21C1A8)" @click="createOrder">{{$t('Commit')}}</Button>
				<div class="checkbox" @click="rule = true">
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
			</div>
		</div>
		
		<overlay :show="rule" class="rule" @click="rule = false">
			<div>
				<div class="ruleTop">
					<div>
						<icon name="fail"></icon>
					</div>
					<span>{{$t('ReceivingInstructions')}}</span>
				</div>
				<div class="ruleBottom">
					<p>1、{{$t('ReceivingInstructionsText01')}}；</p>
					<p>2、{{$t('ReceivingInstructionsText02')}}；</p>
					<p>3、{{$t('ReceivingInstructionsText03')}}；</p>
					<div></div>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Icon , Button , Toast , Overlay } from 'vant'
	import Order from "../../../static/Order.json"
	export default {
		components:{ Icon , Button , Overlay },
		data () {
			return {
				active: 0,
				orderList: [],
				times: '',
				teamAchievements: 0,
				TeamMemberTotal: 0,
				dlevel: '',
				rewardAmount: 0,
				lv: '',
				totalRewardedAmount: 0,
				checked: false,
				ConsumeDT: 0,
				DT: 0,
				value: '',
				Amount: 0,
				rule: false,
			}
		},
		methods:{
			async init(){
				
				// DT数量
				this.DT = this.$fromWei(await this.$contract.getData('referenceToken').methods.balanceOf(this.$address).call())
				
				// 消耗燃料公式
				this.WithdrawCostProp = await this.$contract.getData('config').methods.WithdrawCostProp().call();
				this.USDTToDTProp = await this.$contract.getData('config').methods.USDTToDTProp().call();
				
				// 当前可领取收益/累计领取收益
				var RewardInfo = await this.$contract.getData('reward').methods.RewardInfo(this.$address).call()
				this.rewardAmount = this.$fromWei(RewardInfo.rewardAmount,'','Mwei')
				this.totalRewardedAmount =  this.$fromWei(RewardInfo.totalRewardedAmount,'','Mwei')
				
				if(this.rewardAmount < this.$fromWei(this.$BN(RewardInfo.totalDeposit).sub(this.$BN(RewardInfo.totalRewardedAmount)).toString(),'','Mwei')){
					this.Amount = this.rewardAmount
				}else{
					this.Amount = this.$fromWei(this.$BN(RewardInfo.totalDeposit).sub(this.$BN(RewardInfo.totalRewardedAmount)).toString(),'','Mwei')
				}
				
				// 等级
				this.lv = (await this.$contract.getData('counterManager').methods.UpgradeDLevel().call()).origin
				
			},
			async cretaOrder(stateType,offset){
				// 订单
				var QueryOrders = await this.$contract.getData('controller').methods.QueryOrders(this.$address,stateType,255,offset,10).call()
				// console.log(QueryOrders)
				for(var key = (Number(QueryOrders.len) - 1); key >= 0 ; key--){
					// 创建订单
					var order = new this.$web3.eth.Contract(Order.abi,QueryOrders.orders[key]);
					// 订单状态
					var orderState = await order.methods.orderState().call();
					// 提供帮助总量
					var totalAmount = this.$fromWei(await order.methods.totalAmount().call(),'','Mwei');
					// 当前系统已经支付了多少
					var toHelpedAmount = this.$fromWei(await order.methods.toHelpedAmount().call(),'','Mwei');
					// usdt
					var usdt = this.$fromWei(await this.$contract.getData('usdtToken').methods.balanceOf(QueryOrders.orders[key]).call(),'','Mwei');
					// 当前帮助
					var currentPaymentedAmount = this.$BN(toHelpedAmount).add(this.$BN(usdt)).toNumber()
					// 剩余帮助额度
					var RemainingAmout = this.$BN(toHelpedAmount).sub(this.$BN(currentPaymentedAmount)).toNumber()
					// 创建时间
					var OnCreated = await order.methods.times(0).call();
					// 倒计时开始时间
					var OnCountDownStart = Number(await order.methods.times(4).call());
					// 倒计时结束时间
					var OnCountDownEnd = Number(await order.methods.times(5).call());
					// 当前时间
					var currentTime = await this.$currentTime()
					// 订单类型
					var orderType = await order.methods.orderType().call()
					// console.log(orderState)
					// console.log(await order.methods.contractOwner().call())
					// 当前收益(可查当前最新状态)
					var CurrentProfitInfo = await order.methods.CurrentProfitInfo().call();
					var CurrentP1 = this.$fromWei(CurrentProfitInfo[1],'','Mwei')
					var CurrentP2 = this.$fromWei(CurrentProfitInfo[2],'','Mwei')
					
					if(orderState != '2'){
						if(CurrentP2){
							var getHelpTotal = totalAmount + CurrentP1 + CurrentP2;
							if(orderState == '6' || orderState == '7'){								
								// 可获得帮助总量
								var getHelpedAmountTotal = Number(await order.methods.getHelpedAmountTotal().call())
								
								// 当前获得帮助
								var getHelpedAmount = Number(await order.methods.getHelpedAmount().call())
								var getHelpedAmountString = this.$fromWei(getHelpedAmount.toString(),'','Mwei').toString()
					
								// 当前获得帮助占比
								var currentScale = this.$BN(getHelpedAmount * 100).div(this.$BN(getHelpedAmountTotal)).toNumber()
							}
						}else{
							if(stateType == 2){
								// 可获得帮助总量
								var getHelpedAmountTotal = Number(await order.methods.getHelpedAmountTotal().call())
								var getHelpTotal = this.$fromWei(getHelpedAmountTotal.toString(),'','Mwei').toString();
								
								// 当前获得帮助
								var getHelpedAmount = Number(await order.methods.getHelpedAmount().call())
								var getHelpedAmountString = this.$fromWei(getHelpedAmount.toString(),'','Mwei').toString()
									
								// 当前获得帮助占比
								var currentScale = this.$BN(getHelpedAmount * 100).div(this.$BN(getHelpedAmountTotal)).toNumber()
							}
						}
						// console.log(CurrentProfitInfo,totalAmount)
					}
					
					if(orderType == 2){
						var type = 3
					}else if(orderState == '6' || orderState == '7' || (CurrentP2 && CurrentProfitInfo[0] == '1')){
						var type = 2
					}else if(orderState == '2' || orderState == '5' && CurrentProfitInfo[0] != '1'){
						var type = 1
						var currentScale = this.$BN(currentPaymentedAmount*100).div(this.$BN(totalAmount)).toNumber();
					}
					
					this.orderList.push({
						order: QueryOrders.orders[key],
						totalAmount: totalAmount,
						currentPaymentedAmount: currentPaymentedAmount,
						state: orderState,
						currentScale: currentScale,
						cretaTime: OnCreated,
						timesDown: '00:00:00:00',
						type: type,
						QueuingCompleted: CurrentP2 && orderState == '5' && CurrentProfitInfo[0] == '1' ? true : false,
						OnCountDownEnd: OnCountDownEnd,
						getHelpTotal: getHelpTotal,
						getHelpedAmountString: getHelpedAmountString,
						RemainingAmout: RemainingAmout,
					})
					
					this.orderList.sort(function(a,b){
						return b.cretaTime - a.cretaTime
					})
				}
			},
			input(){
				var value = Number(this.value);
				if(value == '' ||  value == 0){
					this.value = ''
					this.ConsumeDT = ''
					return 
				}else{
					this.ConsumeDT = this.$fromWei(this.$BN(this.$toWei((value).toString(),'Mwei')).mul(this.$BN(this.WithdrawCostProp)).div(this.$BN(this.$toWei('1','szabo'))).mul(this.$BN(this.USDTToDTProp)).toString())
				}
			},
			async createOrder(){
				if(!this.rewardAmount){
					return Toast.fail(this.$t('warningText08')+'0');
				}else if(this.rewardAmount < this.OrderAmountGranularity){
					return Toast.fail(this.$t('warningText09')+this.OrderAmountGranularity);
				}else if(this.value > this.Amount){
					return Toast.fail(this.$t('warningText10'));
				}else if(this.value % 10 != 0){
					return Toast.fail(this.$t('PleaseEnterMultipleOf10'));
				}else if(this.ConsumeDT > this.DT){
					return Toast.fail(this.$t('warningText11'));
				}
				var state = await this.$contract.getData('controller').methods.CreateAwardOrder(this.$web3.utils.toWei(this.value.toString(),'Mwei')).call({from: this.$address})
				console.log(state)
				if(state == '0'){					
					this.$contract.getData('controllerI').methods.CreateAwardOrder(this.$web3.utils.toWei(this.value.toString(),'Mwei')).send({
						from: this.$address,
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , console.error)
					.on('receipt' , result=>{
						this.$router.push('./order')
					})
				}else if(state == 6){
					Toast.fail(this.$t('warningText12'))
				}
			},
		},
		mounted(){
			this.init()
		},
		destroyed() {
			clearInterval(this.times);
		}
	}
</script>

<style lang="scss" scoped>
	.van-tabbar-page{
		position: relative;
		z-index: 1;
	}
	.indexTopBg{
		position: absolute;
		width: 100%;
		height: 280px;
		background:linear-gradient(45deg,#00D1A0 0%,#11614F 100%);
		border-bottom-left-radius: 40px;
		border-bottom-right-radius: 40px;
		top: 0;
		left: 0;
		z-index: -1;
	}
	
	.getHelp{
		padding: 58px 30px 0;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		
		.getHelpDetails{
			width: 100%;
			background: linear-gradient(46deg,#23927B 0%,#1C354A 100%);
			// background-color: #324B6B;
