				top: 28px;
				font-size: 20px;
				color: #fff;
				
				&>span{
					opacity: .5;
				}
				
				&>img{
					width: 17px;
					height: 17px;
					margin-left: 12px;
				}
			}
			
			&>.reveiceIncome{
				font-size: 70px;
				color: #21C17A;
				display: flex;
				flex-direction: column;
				align-items: center;
				margin: 55px 0 45px;
				
				&>div{
					color: #fff;
					font-size: 32px;
					margin-top: 16px;
					color: #fff;
					line-height: 1;
				}
			}
			
			&>.orderInfo{
				position: absolute;
				bottom: 0;
				width: 100%;
				background-color: rgba($color: #172C3E, $alpha: 0.65);
				display: flex;
				align-items: center;
				font-size: 28px;
				color: #A1BDE1;
				border-bottom-left-radius: 10px;
				border-bottom-right-radius: 10px;
				padding: 20px 0;
				
				&>div{
					flex: 1;
					display: flex;
					flex-direction: column;
					align-items: center;
					
					&>span:nth-child(1){
						font-size: 24px;
					}
				}
				
				&>span{
					width: auto;
					height: 40px;
					border-right: 2px solid #21C17A;
				}
			}
		}
	}
	
	
	.income {
		display: flex;
		flex-direction: column;
		width: 100%;
		border-radius: 10px;
		padding: 36px 42px 60px;
		background-color: #273446;
		margin: 26px 0 0;
		&>.checkbox {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 40px;
			&>span {
				font-size: 22px;
				text-decoration: underline;
			}
		}
		&>button {
			width: 400px;
			height: 96px;
			font-size: 40px;
			line-height: 96px;
			margin: 62px auto 0;
			border-radius: 10px;
		}
		&>span {
			color: #A1BDE1;
			font-size: 30px;
			text-align: left;
		}
		&>.input {
			width: 100%;
			height: 112px;
			display: flex;
			align-items: center;
			border-radius: 10px;
			font-size: 36px;
			border: 1px solid #516A89;
			background-color: #101720;
			&:nth-of-type(2) {
				margin-bottom: 30px;
			}
			&:nth-of-type(3) {
				margin-bottom: 16px;
			}
			&>div, &>input {
				width: 432px;
				height: 100%;
			}
			&>div {
				display: flex;
				align-items: center;
				&>input {
					width: 100%;
					height: 100%;
					border: none;
					padding: 0 42px;
					font-size: 26px;
					background-color: transparent;
				}
			}
			&>input {
				border: none;
				padding: 0 42px;
				font-size: 26px;
				background-color: transparent;
			}
			&>span {
				width: calc(100% - 432px);
				line-height: 112px;
				border-left: 1px solid #516A89;
			}
		}
		&>.ConsumeDT{
			&>span:nth-child(2){
				font-size: 40px;
				color: #EB4948;
			}
		}
		&>.Underline{
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding-bottom: 34px;
			border-bottom: 1px solid #516A89;
			font-size: 30px;
			color: #A1BDE1;
			margin-top: 42px;
		}
		&>.AvailableDT{
			font-size: 24px;
