			}
		}
	}
	
	.rule {
		z-index: 2!important;
		padding: 0 40px;
		display: flex;
		justify-content: center;
		align-items: center;
		
		&>div{
			width: 100%;
			padding: 54px 0 250px;
			background-color: #273446;
			color: #A1BDE1;
			border-radius: 10px;
			
			.ruleTop{
				border-bottom: 2px solid #516A89;
				padding-bottom: 24px;
				font-size: 32px;
				
				&>div{
					width:91px;
					height:91px;
					background:rgba(33,193,141,1);
