<template>
	<div>
		<nav-bar :title="title"></nav-bar>
		<div class="navBox">
			<div v-lazy:background="require('@/assets/card_bg.png')">
				<span>{{name}}</span>
				<span>{{$t('inviterCardText01')}}<span>DeFi</span> <span>Ear</span></span>
				<span>{{code}}</span>
				<span>{{$t('inviterCardText02')}}</span>
				<div class="line">
					<i></i>
				</div>
				<div class="qrcode" v-lazy:background="require('@/assets/qrcode_box.png')">
					<div></div>
				</div>
				<span>{{$t('inviterCardText03')}}</span>
			</div>
			<div>
				<Button @click="save" color="transparent">{{$t('SaveCard')}}</Button>
				<i></i>
				<Button
				  color="transparent"
				  v-clipboard:copy="link"
				  v-clipboard:success="onCopySuccess"
				  v-clipboard:error="onCopyError"
				>{{$t('copyLink')}}</Button>
			</div>
		</div>
	</div>
</template>

<script>
	import { Button, Toast } from 'vant'
	import QRCode from 'qrcodejs2'
	import html2canvas from 'html2canvas'
	import navBar from '@/components/nav-bar'
	
	export default {
		components: { navBar, Button },
		data() {
			return {
				title: this.$t('invite'),
				link: '',
				code: '',
				name: '',
			}
		},
		mounted() {
			if (this.$cache.setting) {
				this.code = this.$cache.setting.code
				this.name = this.$cache.setting.name
			}else{
				this.code = '******'
				this.name = ''
			}
			this.link = `${location.href.slice(0, location.href.indexOf('#'))}#/register?code=${this.code}`
			console.log(this.link)
			const qrcode = document.querySelector('.qrcode>div')
			new QRCode(qrcode, this.link)
			qrcode.children.forEach(item => {
				item.style.width = '100%'
				item.style.height = '100%'
			})
		},
		methods: {
			save() {
				const a = document.createElement('a')
				a.download = 'invitation_card'
				html2canvas(document.querySelector('.navBox>div')).then(canvas => {
					a.href = canvas.toDataURL('image/png')
					a.click()
				})
			},
			onCopySuccess() {
				Toast.success(this.$t('CopySuccessful'))
			},
			onCopyError() {
				Toast.fail(this.$t('CopyFailed'))
			}
		}
	}
</script>

<style lang="scss" scoped>
	.navBox {
		padding: 0 38px;
		&>div:last-child {
			width: 590px;
			height: 96px;
			display: flex;
			align-items: center;
			overflow: hidden;
			border-radius: 50px;
			margin: 58px auto 0;
			background-image: linear-gradient(44deg, #21C17A, #21C1A8);
			&>button {
				width: calc(50% - 1px);
				height: 100%;
				font-size: 40px;
				line-height: 96px;
			}
			&>i {
				width: 2px;
				height: 76px;
				background-color: #18222F;
			}
		}
		&>div:first-child {
			color: #8FACD2;
			padding: 330px 0 22px;
			background-size: 100% 100% !important;
			background-repeat: no-repeat !important;
			display: flex;
			align-items: center;
			flex-direction: column;
			border-radius: 20px;
			box-shadow: 0 6px 4px #000000;
			&>.qrcode {
				width: 238px;
				height: 239px;
				margin-top: 24px;
				margin-bottom: 22px;
				padding: 12px;
				padding-top: 14px;
				background-size: 100% 100% !important;
				background-repeat: no-repeat !important;
				&>div {
					width: 100%;
					height: 100%;
					padding: 9px;
					border-radius: 6px;
					background-color: #fff;
				}
			}
			&>.line {
				width: 100%;
				height: 60px;
				padding: 0 47px;
				display: flex;
				align-items: center;
				position: relative;
				overflow: hidden;
				&>i {
					display: block;
					width: 100%;
					border-bottom: 2px dashed #18222F;
				}
				&::before {
					content: '';
					width: 60px;
					height: 60px;
					position: absolute;
					left: -30px;
					border-radius: 30px;
					background-color: #18222F;
				}
				&::after {
					content: '';
					width: 60px;
					height: 60px;
					position: absolute;
					right: -30px;
					border-radius: 30px;
					background-color: #18222F;
				}
			}
			&>span {
				font-size: 50px;
				&:nth-of-type(2) {
					margin-top: 14px;
					margin-bottom: 16px;
					&>span:first-child {
						color: #21C18C;
					}
					&>span:last-child {
						color: #fff;
					}
				}
				&:nth-of-type(3) {
					color: #21C18C;
					font-size: 90px;
				}
				&:nth-of-type(4) {
					font-size: 30px;
					margin-top: 2px;
					margin-bottom: 18px;
				}
				&:last-child {
					font-size: 24px;
				}
			}
		}
	}
</style>
