import Vue from 'vue'
import Web3 from 'web3'
import Relationship from "./Relationship.json"
import USDTToken from "./USDTToken.json"
import Controller from "./Controller.json"
import Config from "./Config.json"
import Swap from "./Swap.json"
import SwapState from "./SwapState.json"
import ReferenceToken from "./ReferenceToken.json"
import ControllerState from "./ControllerState.json"
import Phoenix from "./Phoenix.json"
import PhoenixState from "./PhoenixState.json"
import CounterManager from "./CounterManager.json"
import CounterManagerState from "./CounterManagerState.json"
import Reward from "./Reward.json"
import AssertPool from "./AssertPool.json"
import AssertPoolState from "./AssertPoolState.json"
import AssertPoolAwards from "./AssertPoolAwards.json"
import AssertPoolAwardsState from "./AssertPoolAwardsState.json"
var $web3, guide = false,IWeb3;
if (window.ethereum) {
	// ethereum
    $web3 = new Web3(new Web3.providers.HttpProvider('https://mainnet.infura.io/v3/1aa4bcf8fe974697914d0ad9002f300b'))
	IWeb3 = new Web3(ethereum)
} else if (window.web3) {
	// web3.currentProvider
    $web3 = new Web3(new Web3.providers.HttpProvider('https://mainnet.infura.io/v3/1aa4bcf8fe974697914d0ad9002f300b'))
	IWeb3 = new Web3(web3.currentProvider)
} else {
	$web3 = new Web3()
	IWeb3 = new Web3()
	guide = true;
}

class Contract {
	#data;
	
	constructor() {
	    this.#data = {}
	}
	
	setData(networkId) {
		this.#data = {
			usdtTokenI: new IWeb3.eth.Contract(USDTToken.abi, '0xdac17f958d2ee523a2206206994597c13d831ec7'),
			swapI: new IWeb3.eth.Contract(Swap.abi, Swap.networks[networkId].address),
			controllerI: new IWeb3.eth.Contract(Controller.abi, Controller.networks[networkId].address),
			relationshipI: new IWeb3.eth.Contract(Relationship.abi, Relationship.networks[networkId].address),
			counterManagerI: new IWeb3.eth.Contract(CounterManager.abi, CounterManager.networks[networkId].address),
			assertPoolAwardsI: new IWeb3.eth.Contract(AssertPoolAwards.abi, AssertPoolAwards.networks[networkId].address),
			phoenixI: new IWeb3.eth.Contract(Phoenix.abi, Phoenix.networks[networkId].address),
			// 不需要交易的合约
			usdtToken: new $web3.eth.Contract(USDTToken.abi, '0xdac17f958d2ee523a2206206994597c13d831ec7'),
			controllerState: new $web3.eth.Contract(ControllerState.abi, Controller.networks[networkId].address),
			config: new $web3.eth.Contract(Config.abi, Config.networks[networkId].address),
			swapState: new $web3.eth.Contract(SwapState.abi, Swap.networks[networkId].address),
			referenceToken: new $web3.eth.Contract(ReferenceToken.abi, ReferenceToken.networks[networkId].address),
			phoenix: new $web3.eth.Contract(Phoenix.abi, Phoenix.networks[networkId].address),
			phoenixState: new $web3.eth.Contract(PhoenixState.abi, Phoenix.networks[networkId].address),
			counterManagerState: new $web3.eth.Contract(CounterManagerState.abi, CounterManager.networks[networkId].address),
			reward: new $web3.eth.Contract(Reward.abi, Reward.networks[networkId].address),
			assertPool: new $web3.eth.Contract(AssertPool.abi, AssertPool.networks[networkId].address),
			assertPoolState: new $web3.eth.Contract(AssertPoolState.abi, AssertPool.networks[networkId].address),
			assertPoolAwards: new $web3.eth.Contract(AssertPoolAwards.abi, AssertPoolAwards.networks[networkId].address),
			assertPoolAwardsState: new $web3.eth.Contract(AssertPoolAwardsState.abi, AssertPoolAwards.networks[networkId].address),
			swap: new $web3.eth.Contract(Swap.abi, Swap.networks[networkId].address),
			controller: new $web3.eth.Contract(Controller.abi, Controller.networks[networkId].address),
			relationship: new $web3.eth.Contract(Relationship.abi, Relationship.networks[networkId].address),
			counterManager: new $web3.eth.Contract(CounterManager.abi, CounterManager.networks[networkId].address),
		}
	}
	
	getData(method) {
		return this.#data[method]
	}
}
const contract = new Contract()

const fromWei = (balance, digit , type = 'ether') => {
    if (digit) {
        return Number($web3.utils.fromWei(balance, type)).toFixed(digit)
    }
    return Number($web3.utils.fromWei(balance, type))
}

const numFixed = (num , digit) => {
	var num = num.toString();
	var index = num.indexOf('.');
	if(index !== -1){
		num = num.substring(0, digit + index + 1)
	}else{
		num = num.substring(0)
	}
	return parseFloat(num)
}

const upFixed = (num, fix) => {
	// num为原数字，fix是保留的小数位数
	let result = '0'
	if (Number(num) && fix > 0) { // 简单的做个判断
		fix = +fix || 2
		num = num + ''
		if (/e/.test(num)) { // 如果是包含e字符的数字直接返回
			result = num
		} else if (!/\./.test(num)) { // 如果没有小数点
			result = num + `.${Array(fix + 1).join('0')}`
		} else { // 如果有小数点
			num = num + `${Array(fix + 1).join('0')}`
			let reg = new RegExp(`-?\\d*\\.\\d{0,${fix}}`)
			let floorStr = reg.exec(num)[0]
			if (+floorStr >= +num) {
				result = floorStr
			} else {
				let floorNumber = +floorStr + +`0.${Array(fix).join('0')}1`
				let point = /\./.test(floorNumber + '') ? '' : '.'
				let floorStr2 = floorNumber + point + `${Array(fix + 1).join('0')}`
				result = reg.exec(floorStr2)[0]
			}
		}
	}
	return result
}

const toWei = (balance,type) => {
	if(type){
		return $web3.utils.toWei(balance, type)
	}else{
		return $web3.utils.toWei(balance, 'ether')
	}
}

const toTime = (times , type) => {
	function add(m){return m<10?'0'+m:m }
	var time = new Date(times * 1000);
	var y = time.getFullYear();
	var m = time.getMonth()+1;
	var d = time.getDate();
	var h = time.getHours();
	var mm = time.getMinutes();

	if (type === 'Y/M/D') {
		return y+'.'+add(m)+'.'+add(d);
	} else if (type === 'Y-M-D H:i') {
		return y+'-'+add(m)+'-'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'M/D H:i'){
		return add(m)+'/'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'Y/M/D H:i'){
		return y+'/'+add(m)+'/'+add(d)+' '+add(h)+':'+add(mm);
	}else if(type === 'YMD'){
		return y+add(m)+add(d);
	}else if(type === 'H:i'){
		return add(h)+':'+add(mm);
	}
}

const BN = (number) => {
	if(!guide){
		var BN = new $web3.utils.BN(number); 
		return BN		
	}
}

const currentTime = async () => {
	return (await $web3.eth.getBlock('latest').then()).timestamp;
	// return Math.floor(new Date().getTime()/1000)
}

export { $web3, contract, fromWei, toWei , toTime , guide , upFixed , numFixed , BN , currentTime }
