<template>
	<div>
		<nav-bar :title='title'></nav-bar>
		<div class="navBox">
			<div class="provideHelpBox">
				<div class="provideHelpTitle">{{$t('Provide')}}</div>
				<div class="provideHelpInput">
					<input type="number" :placeholder="$t('EnterMultipleOf10')" v-model="number" @input="Input" />
					<div>USDT</div>
				</div>
				<div class="joinInfo">
					<!-- <div>
						<span>最小参与数量</span>
						<div>
							<span>{{minJoin}}</span>
							<span>USDT</span>
						</div>
					</div>
					<div>
						<span>最大参与数量</span>
						<div>
							<span>{{OrderAmountMaxLimit}}</span>
							<span>USDT</span>
						</div>
					</div> -->
					{{$t('range')}}：{{minJoin}}~{{OrderAmountMaxLimit}}
				</div>
				<!-- <div class="provideHelpInput">
					<input type="number" placeholder="即时付款≥50%" v-model="multiple" />
					<div>USDT</div>
				</div> -->
				<div class="provideHelpTitle">{{$t('PrepaidRatio')}}</div>
				<div class="provideHelpSelect">
					<div :class="{active:item.number == active}" v-for="(item,index) in selectList" :key="index" @click="multipleFun(item.number)">{{item.number}}%</div>
				</div>
				<div class="provideHelpMultiple">
					<span>{{$t('PrepaymentAmount')}}</span>
					<span>- {{multiple}} USDT</span>
				</div>
				<div class="provideHelpBalance">{{$t('Available')}} {{usdt}} USDT</div>
				<Button :disabled='RemainingDepositedUSDT == 0 || !checked' @click='CreateOrder'>{{$t('Commit')}}</Button>
				<div class="checkbox" @click="checked = !checked">
					<icon name="success" :class="checked ? 'active' : ''"></icon>
					<span>{{$t('IAgree')}}<span @click.stop="joinRules = true">《{{$t('ParticipationRules')}}》</span>{{$t('AndUnderstandTheRisks')}}</span>
				</div>
			</div>
		</div>
		
		<authorization
		:show="authorizedAccess" 
		:click="authorizedAccessFun"
		:needAuthorization='multiple' 
		:tokenType="'USDT'"
		:authorizationEnds="authorizationEnds"
		@authorizationEnds="end"
		></authorization>
		
		<overlay :show="joinRules" class="joinRules" @click="joinRules = false">
			<div>
				<div class="title">{{$t('ParticipationRulesText01')}}</div>
				<div class="content">
					<p>{{$t('ParticipationRulesText02')}}</p>
					<p>{{$t('ParticipationRulesText03')}}</p>
					<p>{{$t('ParticipationRulesText04')}}</p>
					<p>{{$t('ParticipationRulesText05')}}</p>
					<p>{{$t('ParticipationRulesText06')}}</p>
					<p>{{$t('ParticipationRulesText07')}}</p>
					<p>{{$t('ParticipationRulesText08')}}</p>
					<p>{{$t('ParticipationRulesText09')}}</p>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import navBar from '@/components/nav-bar'
	import { Button, Icon , Toast , Overlay } from 'vant'
	import Authorization from '@/components/Authorization.vue'
	import Order from "../../../static/Order.json"
	export default {
		components: { navBar , Button , Icon , Authorization , Toast , Overlay },
		data() {
			return {
				checked: false,
				authorizedAccess: false,
				authorizationEnds: false,
				selectList: [
					{number: 50},
					{number: 60},
					{number: 70},
					{number: 80},
					{number: 90},
					{number: 100}
				],
				active: '',
				number: '',
				multiple: 0,
				usdt: '',
				OrderAmountMinLimit: '',
				OrderAmountMaxLimit: '',
				minJoin: '',
				joinRules: false,
				title: this.$t('ProvideHelp'),
				RemainingDepositedUSDT: '',
			}
		},
		methods:{
			async init(){
				this.authorizedAccess = false;
				
				//当前时间
				var currentTime = await this.$currentTime()
				
				// usdt授权额度
				this.USDTAuthorizedAccess = this.$fromWei(await this.$contract.getData('usdtToken').methods.allowance(this.$address,this.$contract.getData('controller')._address).call({from:this.$address}),'','Mwei');
			
				// usdt
				this.usdt = this.$fromWei(await this.$contract.getData('usdtToken').methods.balanceOf(this.$address).call(),'','Mwei')
				
				// 创建订单最小限制
				this.OrderAmountMinLimit = this.$fromWei(await this.$contract.getData('config').methods.OrderAmountMinLimit().call(),'','Mwei')
				
				// 创建订单最小倍数
				this.OrderPaymentedMinPart = this.$fromWei(await this.$contract.getData('config').methods.OrderPaymentedMinPart().call(),'','szabo') * 100;
				
				// 日投入上限
				this.DepositedUSDMaxLimit = await this.$contract.getData('config').methods.DepositedUSDMaxLimit().call();
				
				// 当天日投入量
				this.depositedLimitMapping = await this.$contract.getData('controllerState').methods.depositedLimitMapping((new Date(new Date(currentTime*1000).toLocaleDateString()).getTime() / 1000) + 8 * 3600).call();
				
				// 剩余可参与的USDT量(日)
				this.RemainingDepositedUSDT = this.$fromWei(this.$BN(this.DepositedUSDMaxLimit).sub(this.$BN(this.depositedLimitMapping)).toString(),'','Mwei')
				
				var GetOrder = await this.$contract.getData('controller').methods.GetOrder(this.$address,0).call();
				console.log(this.RemainingDepositedUSDT)
				if(GetOrder.total != '0'){
					var id = Number(GetOrder.total)-1;
					var getOrder = (await this.$contract.getData('controller').methods.GetOrder(this.$address,id).call()).order;
					var order = new this.$web3.eth.Contract(Order.abi,getOrder);
					var total =  this.$fromWei(await order.methods.totalAmount().call(),'','Mwei');
					console.log(total)
					if(total>=1000){
						this.minJoin = total;
					}else{
						this.minJoin = total+10;
					}
					
					if((Number(GetOrder.total)+1) <= 5){
						var OrderAmountAppendQuota = this.$fromWei(await this.$contract.getData('config').methods.OrderAmountAppendQuota().call(),'','Mwei')
						this.OrderAmountMaxLimit = this.$BN(Number(GetOrder.total)+1).mul(this.$BN(OrderAmountAppendQuota)).toNumber()
					}else{
						// 创建订单最大限制
						this.OrderAmountMaxLimit = this.$fromWei(await this.$contract.getData('config').methods.OrderAmountMaxLimit().call(),'','Mwei')
					}
				}else{
					this.minJoin = this.OrderAmountMinLimit;
					this.OrderAmountMaxLimit = this.$fromWei(await this.$contract.getData('config').methods.OrderAmountAppendQuota().call(),'','Mwei')
					// this.$fromWei(await this.$contract.getData('config').methods.OrderAmountMaxLimit().call(),'','Mwei')
				}
				
				// 获取我的推荐人
				this.GetIntroducer = await this.$contract.getData('relationship').methods.GetIntroducer(this.$address).call()
			},
			async CreateOrder(){
				var number = Number(this.number),
					multiple = Number(this.multiple);

				if(!number){
					return Toast.fail(this.$t('warningText01'));
				}else if(!multiple){
					return Toast.fail(this.$t('warningText02'));
				}else if(!parseInt(this.GetIntroducer,16)){
					Toast.fail(this.$t('warningText03'));
					let timeOut = setTimeout(() => {
						clearTimeout(timeOut)
						this.$router.push('./register')
					}, 2000)
					return 
				}else if(number % 10 != 0){
					return Toast.fail(this.$t('warningText04'));
				}else if(number < this.minJoin && this.minJoin < 1000){
					return Toast.fail(this.$t('warningText04'));
				}else if(number < this.minJoin && this.minJoin >= 1000){
					return Toast.fail(this.$t('warningText04'));
				}else if(number > this.OrderAmountMaxLimit){
					return Toast.fail(this.$t('warningText04'));
				}else if(this.$BN(multiple * 100).div(this.$BN(number)).toNumber() < this.OrderPaymentedMinPart){
					// return Toast.fail('最小即时付款倍数为'+this.OrderPaymentedMinPart);
					return Toast.fail(this.$t('warningText05')+'≥'+this.OrderPaymentedMinPart+'%');
				}else if(number > this.RemainingDepositedUSDT){
					return Toast.fail(this.$t('warningText04'));
				}else if(multiple > this.usdt){
					return Toast.fail(this.$t('warningText06'));
				}else if(multiple > this.USDTAuthorizedAccess){
					this.authorizedAccess = true
					return 
				}
				
				var state = await this.$contract.getData('controller').methods.CreateOrder(this.$web3.utils.toWei(this.number.toString(),'Mwei'),this.$web3.utils.toWei(this.multiple.toString(),'Mwei')).call({from: this.$address})
				console.log(state)
				if(state == '0'){					
					this.$contract.getData('controllerI').methods.CreateOrder(this.$web3.utils.toWei(this.number.toString(),'Mwei'),this.$web3.utils.toWei(this.multiple.toString(),'Mwei')).send({
						from: this.$address,
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , console.error)
					.on('receipt' , result=>{
						this.$router.back()
					})
				}else if(state == '4'){
					return Toast.fail(this.$t('warningText07'));
				}
			},
			authorizedAccessFun(value){
				this.$contract.getData('usdtTokenI').methods.approve(this.$contract.getData('controller')._address,this.$web3.utils.toWei(value.toString(),'Mwei')).send({
					from : this.$address
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err)
				}).on('receipt' ,async result=>{
					this.init()
					this.authorizationEnds = true;
					var state = await this.$contract.getData('controller').methods.CreateOrder(this.$web3.utils.toWei(this.number.toString(),'Mwei'),this.$web3.utils.toWei(this.multiple.toString(),'Mwei')).call({from: this.$address})
					if(state == '0'){					
						this.$contract.getData('controllerI').methods.CreateOrder(this.$web3.utils.toWei(this.number.toString(),'Mwei'),this.$web3.utils.toWei(this.multiple.toString(),'Mwei')).send({
							from: this.$address,
						}).on('transactionHash' , txHash =>{
							console.log(txHash)
						}).on('error' , console.error)
						.on('receipt' , result=>{
							this.$router.back()
						})
					}else if(state == '4'){
						Toast.fail(this.$t('warningText07'));
					}
				})
			},
			multipleFun(value){
				this.active = value;
				this.multiple = this.number * value / 100
			},
			Input(){
				if(this.active){
					this.multiple = this.number * this.active / 100
				}
				if(this.number == ''){
					this.number = ''
					this.multiple = 0
				}
			},
			end(){
				this.authorizedAccess = false;
			},
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss" scoped>
	.navBox{
		padding: 0 28px;
	}
	.provideHelpBox{
		width: 100%;
		height: 1016px;
		background-color: #273446;
		border-radius: 12px;
		padding: 36px 42px 0;
		box-shadow: 0 6px 4px #000000;
		
		&>.joinInfo{
			display: flex;
			justify-content: space-between;
			margin-bottom: 36px;
			font-size: 28px;
			color: #A1BDE1;
			
			&>div{
				width: 282px;
				height: 112px;
				background-color: #2A405C;
				border-radius: 12px;
				color: #A1BDE1;
				padding-left: 18px;
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				justify-content: center;
				border: 2px solid #516A89;
				
				&>span{
					font-size: 26px;
				}
				
				&>div{
					display: flex;
					
					&>span:nth-child(1){
						font-size: 32px;
					}
					
					&>span:nth-child(2){
						font-size: 20px;
						margin: 5px 0 0 5px;
					}
				}
			}
		}
		
		&>.provideHelpTitle{
			color: #A1BDE1;
			font-size: 30px;
			text-align: left;
			margin-bottom: 20px;
		}
		
		&>.provideHelpMultiple{
			font-size: 30px;
			color: #A1BDE1;
			padding-bottom: 34px;
			border-bottom: 1px solid #516A89;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 40px 0 30px;
			
			&>span:nth-child(2){
				font-size: 40px;
				color: #EB4948;
			}
		}
		
		&>.provideHelpInput{
			display: flex;
			width: 100%;
			height: 112px;
			font-size: 36px;
			border: 2px solid #4d6784;
			border-radius: 10px;
			margin-bottom: 36px;
			
			&>input{
				width: 432px;
				height: 112px;
				background-color: transparent;
				border: none;
				padding-left: 30px;
				font-size: 26px;
			}
			
			&>div{
				flex: 1;
				height: 100%;
				color: #A1BDE1;
				border-left: 2px solid #4d6784;
				display: flex;
				justify-content: center;
				align-items: center;
				
				&>img{
					width: 43px;
					margin-right: 34px;
				}
			}
		}
		
		&>.provideHelpBalance{
			font-size: 30px;
			text-align: left;
			color: #A1BDE1;
			margin: 16px 0 44px;
		}
		
		&>.provideHelpSelect{
			display: flex;
			justify-content: space-between;
			
			&>div{
				width: 90px;
				height: 72px;
				border-radius: 12px;
				background-color: rgba(#86A0C2,.1);
				font-size: 24px;
				display: flex;
				align-items: center;
				justify-content: center;
				border: 2px solid #516A89;
				color: #516A89;
			}
			
			&>.active{
				background-color: transparent;
				color: #21C17A;
				border: 2px solid #21C17A;
			}
		}
		
		&>.checkbox {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			margin-top: 40px;
			font-size: 30px;
			
			&>i {
				width: 40px;
				height: 40px;
				line-height: 40px;
				color: transparent;
				margin-right: 15px;
				border-radius: 10px;
				border: 2px solid #21C17A;
				&.active {
					color: #fff;
					background-color: #21C17A;
				}
			}
			&>span {
				font-size: 22px;
				text-align: left;
				&>span {
					color: #1FA36E;
				}
			}
		}
		
		&>button {
			width: 400px;
			height: 96px;
			color: #fff;
			border: none;
			margin: 0 auto;
			font-size: 40px;
			margin-top: 48px;
			line-height: 96px;
			border-radius: 50px;
			background-image: linear-gradient(44deg, #21C17A, #21C1A8);
		}
	}
	
	.joinRules{
		padding: 116px 40px 62px;
		display: flex;
		align-items: center;
		
		&>div{
			width: 100%;
			// height: 100%;
			color: #A1BDE1;
			border-radius: 10px;
			background-color: #273446;
			
			&>.title{
				width: 100%;
				height: 110px;
				font-size: 36px;
				line-height: 110px;
				border-bottom: 1px solid #516A89;
			}
			
			&>.content{
				width: 100%;
				// height: calc(100% - 110px);
				// flex: 1;
				padding: 30px 20px 30px 30px;
				font-size: 28px;
				overflow-y: scroll;
				text-align: left;
				line-height: 1.6;
				
				.marginBottom{
					margin-bottom: 20px;
				}
			}
		}
	}
</style>
